import React from 'react'
import FramerText from '../hocs/FramerText'

let cached = 0

const Svg84 = ({ scenario, ...rest }) => {
  const value = scenario ? scenario.value : 132
  const lastValue = cached

  cached = value

  return (
    <svg width={15} height={9} {...rest}>
      <text
        transform="translate(-1039 -176)"
        fill="#122373"
        fillRule="evenodd"
        fontFamily="Montserrat-SemiBold, Montserrat"
        fontSize={10}
        fontWeight={500}
        letterSpacing={-0.229}
      >
        <FramerText
          value={value}
          lastValue={lastValue}
          order={scenario && scenario.order ? scenario.order : 1}
          x={1039}
          y={184}
        />
      </text>
    </svg>
  )
}
export default Svg84
